html,
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
img {
  display: block;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
fieldset,
img {
  border: 0;
}
address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}
ul,
ol,
li {
  list-style: none;
}
caption,
th {
  text-align: left;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
q:before,
q:after {
  content: '';
}
abbr,
acronym {
  border: 0;
  font-variant: normal;
}
sup {
  vertical-align: text-top;
}
sub {
  vertical-align: text-bottom;
}
input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  font-size: 100%;
}
legend {
  color: #000;
}
input[type='button'],
input[type='submit'],
input[type='reset'] {
  -webkit-appearance: none;
}
body {
  min-width: 800px;
  font: normal 12px/1.5 PingFangSC-Regular, Microsoft Yahei, tahoma, sans-serif;
}
strong,
b {
  font-weight: bold;
}
textarea,
input {
  resize: none;
  outline: none;
}
a {
  text-decoration: none;
  outline: none;
}
a:hover {
  text-decoration: none;
}
h1 {
  font-size: 20px;
}
h2 {
  font-size: 16px;
}
h3 {
  font-size: 14px;
}
h4,
h5,
h6 {
  font-size: 12px;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.cf:before,
.cf:after {
  content: '';
  display: table;
}
.cf:after {
  clear: both;
}
.cf {
  zoom: 1;
}
.hidden {
  display: none;
}
.iframe-blank {
  border: none;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border: 0px;
  opacity: 0;
  background: #fff;
  filter: progid:DXImageTransform.Microsoft.Alpha(opacity=0);
  position: absolute;
  display: block;
}
.plt-clt .g-header,
.plt-clt .g-miniHeader,
.plt-clt .g-search,
.plt-clt .g-sitemap,
.plt-clt .g-copyright {
  clear: both;
  display: none;
}
.blk {
  margin-left: auto;
  margin-right: auto;
}
.alCenter {
  text-align: center;
}
.mt10 {
  margin-top: 10px;
}
.c-2nd {
  color: #9a9a9a;
}
* {
  box-sizing: content-box;
}
.pointer {
  cursor: pointer;
}
.abs {
  position: absolute;
}
.rel {
  position: relative;
}
.mr2 {
  margin-right: 2px;
}
.ml3 {
  margin-left: 3px;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.mt17 {
  margin-top: 17px;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.one-line {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.two-line {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  /* autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 2;
}
.three-line {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  /* autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 3;
}
/* base 三种尺寸 */
.header-wrapper {
  background: #fff;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.2);
}
.header-wrapper .header-inner .search-wrapper .search-box {
  background: #FAFAFA;
}
.header-wrapper .header-inner .side-wrapper .side-item .icon-item {
  color: #666;
}
.header-wrapper .header-inner .side-wrapper .side-item .icon-item .text {
  text-shadow: none;
}
.header-wrapper .header-inner .logo .header-nav .header-nav-item {
  color: #666;
}
.header-wrapper .header-inner .logo .header-nav .header-nav-item:hover {
  color: #007AFF;
}
@media screen and (max-width: 1680px) {
  .library .library-inner {
    width: 1120px;
  }
  .library-content {
    width: 1160px;
    min-height: 540px;
  }
  .library-content .no-data {
    position: absolute;
    top: 160px;
    left: 450px;
    width: 300px;
    height: 185px;
  }
  .exhibition-area .base {
    width: 152px;
  }
  .exhibition-area .base .topImg {
    height: 200px;
  }
  .exhibition-area .base .topImg img {
    width: 100%;
    height: 100%;
  }
  .exhibition-area li {
    margin-right: 16px;
  }
  .exhibition-area li:nth-child(7n) {
    margin: 0;
  }
}
@media screen and (min-width: 1680px) {
  .library .library-inner {
    width: 1600px;
  }
  .library-content {
    width: 1640px;
    min-height: 600px;
  }
  .library-content .no-data {
    position: absolute;
    top: 200px;
    left: 690px;
    width: 300px;
    height: 185px;
  }
  .exhibition-area .base {
    width: 196px;
  }
  .exhibition-area .base .topImg {
    height: 258px;
  }
  .exhibition-area .base .topImg img {
    width: 100%;
    height: 100%;
  }
  .exhibition-area li {
    margin-right: 10px;
  }
  .exhibition-area li:nth-child(8n) {
    margin: 0;
  }
}
.library {
  width: 100%;
  margin-top: 60px;
  padding: 10px 0;
  background: #FAFAFA;
}
.library .library-inner {
  margin: 0 auto;
  padding: 0 20px;
  background: #FFFFFF;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.05);
}
.classesLibrary {
  padding: 20px 0;
  border-bottom: 1px solid #E4E4E4;
}
.classesLibrary li {
  display: inline-block;
  padding: 5px 21px 6px;
  font-size: 14px;
  color: #333333;
  border-radius: 15px;
  cursor: pointer;
}
.classesLibrary li:hover {
  color: #007AFF;
}
.classesLibrary .active {
  color: #FFFFFF;
  background: #007AFF;
}
.classesLibrary .active:hover {
  color: #FFFFFF;
}
.library-content {
  position: relative;
  margin: 0 auto;
  padding: 0 20px;
}
.library-content .library-filter {
  position: relative;
  height: 20px;
  line-height: 20px;
  padding: 20px 0;
}
.library-content .library-filter .filter-tab .tip {
  display: inline-block;
  margin-right: 20px;
  font-size: 14px;
  color: #343434;
  cursor: pointer;
}
.library-content .library-filter .filter-tab .tip:hover {
  color: #007AFF;
}
.library-content .library-filter .filter-tab .active {
  color: #007AFF;
  border-bottom: 2px solid #007AFF;
}
.library-content .library-filter .mini-pagination {
  position: absolute;
  top: 20px;
  right: 0;
}
.library-content .library-filter .mini-pagination .text,
.library-content .library-filter .mini-pagination .page {
  font-size: 12px;
  color: #B2B2B2;
  margin-right: 10px;
}
.library-content .library-filter .mini-pagination .num {
  color: #666666;
}
.library-content .library-filter .mini-pagination .prev-icon,
.library-content .library-filter .mini-pagination .next-icon {
  display: inline-block;
  width: 20px;
  height: 20px;
  background-size: contain;
  vertical-align: bottom;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}
.library-content .library-filter .mini-pagination .prev-icon {
  margin-right: 10px;
  background: url(data:image/png;base64,) no-repeat center;
}
.library-content .library-filter .mini-pagination .prev-hover {
  background: url(data:image/png;base64,) no-repeat center;
}
.library-content .library-filter .mini-pagination .next-icon {
  background: url(data:image/png;base64,) no-repeat center;
}
.library-content .library-filter .mini-pagination .next-hover {
  background: url(data:image/png;base64,) no-repeat center;
}
.library-content .library-pagination {
  margin: 30px 0 120px;
}
